html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html body {
  margin: 0;
  overflow-x: hidden;
  min-height: 100vh;
  max-height: 100%;
}

html body {
  font-family: 'Inter', sans-serif;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.text {
  font-weight: normal;
  color: #000000;
}
